.add-country-container {
  position: fixed;
  overflow: hidden;
  top: 72px;
  left: 250px;
  right: -18px;
  bottom: 0;
  transform: translateX(-100%);
  z-index: -1;
  opacity: 0;
  @include trans;
  @include tablet {
    left: 0;
    right: 0;
    top: 0; } }
.add-country {
  position: relative;
  background-color: #FFF;
  overflow: auto;
  width: 100%;
  height: 100%;
  border-left: 8px solid #9452ae;
  padding: 32px;
  @include mob {
    padding: 0; }

  .category-list {
    padding-right: 60px; } }

.add-country-close {
  width: 30px;
  height: 32px;
  font-size: 32px;
  position: absolute;
  right: 30px;
  top: 40px;
  @include mob {
    top: 16px;
    right: 16px; } }

#add-country {
  display: none; }

#add-country {
  &:checked {
    & ~ .add-country-label {
      box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.1);
      background-color: #9452ae; }
    & ~ .add-country-container {
      opacity: 1;
      transform: translateX(0%);
      z-index: 90;
      @include tablet {
        z-index: 2147483648; } } } }

.category-checkbox {
  display: none;
  visibility: hidden; }

.category-checkbox:checked ~ .countries-list .countries-col .letter-list .country-option {
    display: none; }

.category-checkbox_latin:checked {
  & ~ .category-list .category-row label[for="category-checkbox-latin"] {
    background-color: #eee; }
  & ~ .countries-list .countries-col .letter-list .country-option[data-category="latin"] {
    display: block; } }

