.main {
  background-color: #F5F5F5;
  position: relative;
  box-sizing: border-box;
  margin-left: 250px;
  min-height: 100vh;
  position: relative;
  padding-bottom: 144px;
  padding-top: 72px;
  @include r(1025) {
    @include rmin(768) {
      padding-bottom: 176px; } }
  @include tablet {
    margin-left: 0; }
  @include mob {
    padding-bottom: 128px;
    padding-top: 64px; } }

.footer {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0; }

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 250px;
  @include tablet {
    height: auto;
    bottom: auto;
    width: 100%; } }

.sidebar-desktop {
  @include tablet {
    display: none; } }
.header {
  position: fixed;
  top: 0;
  left: 250px;
  right: 0;
  z-index: 99;
  @include tablet {
    display: none; } }

.cat {
  margin-bottom: 100px;
  @include tablet {
    margin-bottom: 0; } }
